// Importacion Estilos Globales
@import 'EstilosGlobales/EstilosGlobales.scss';

$porcentajeDeveloper:0.30;
$porcentajeDesign:1-$porcentajeDeveloper;
$dev: $porcentajeDeveloper*360deg;
$des: $porcentajeDesign*360deg;


// ---- ESTILOS COMPONENTE SECCION HABILIDADES ----
.contenedor-Habilidades {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    background-image: url('../../../Imagenes/fondo-habilidades.png'), linear-gradient(0deg, rgba(217, 221, 255, 0.263) 5%, rgba(119, 18, 18, 0.052) 88%);;
    background-blend-mode:multiply;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    .parrafoHabilidades {
        width: 100%
    }

}
.contenedor-seccion-tecnologias-Habilidades {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    padding: 30px;
    width: 100%;

    section:nth-child(1) {
        text-align: center;
        width: 30%;
    }
    section:nth-child(2) {

    }
    section:nth-child(3) {
        text-align: center;
        width: 30%;
    }
}

.donut-hole {
    stroke: #00000050;
    stroke-width:0.1px;

    fill: #ffffff4a;

}


.donut-segment {
    transform-origin: center;
}

.donut-segment-1 {
    opacity: 0.5;
    stroke: $color2;
}

.donut-segment-2 {
    opacity: 0.5;
    stroke: $color4;
}


.donut-text {
    font-family: Arial, Helvetica, sans-serif;
    
}

.donut-design {
    fill: #d2d2d2;
    font-size: 3pt;

}

.donut-dev {
    fill: #d2d2d2;
    font-size: 3pt;

}

.donut-data-dev {
    font-size: 1.9pt;
    fill: rgb(255, 255, 255);
}
.donut-data-design {
    font-size: 1.7pt;
    fill: rgb(255, 255, 255);
}




